/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

body {
  background-color: #f4f4f9;
  color: #333;
}

/* Navigation Bar */
.navbar {
  background-color: #2c3e50;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar .logo h1 {
  color: #ecf0f1;
}

.navbar .nav-links ul {
  list-style: none;
  display: flex;
}

.navbar .nav-links ul li {
  margin: 0 15px;
}

.navbar .nav-links ul li a {
  color: #ecf0f1;
  text-decoration: none;
  font-size: 18px;
}

.navbar .nav-links ul li a:hover {
  color: #3498db;
}

/* Main Content */
.home-page {
  padding: 40px;
}

.home-page h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2em;
}

.blog-posts {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.blog-posts .post {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.blog-posts .post h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
}

.blog-posts .post .excerpt {
  font-size: 1em;
  color: #555;
}

.blog-posts .post a {
  text-decoration: none;
  color: #2c3e50;
}

.blog-posts .post a:hover {
  color: #3498db;
}

/* Footer */
.footer {
  background-color: #2c3e50;
  color: #ecf0f1;
  text-align: center;
  padding: 20px;
  margin-top: 50px;
}

.footer p {
  font-size: 16px;
}

/* About and Contact Pages */
.about-page,
.contact-page {
  padding: 40px;
  text-align: center;
}

.about-page h2,
.contact-page h2 {
  margin-bottom: 20px;
}

/* Blog Post */
.blog-post {
  padding: 40px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  max-width: 800px;
}

.blog-post h2 {
  margin-bottom: 20px;
  font-size: 2em;
}

.blog-post p {
  font-size: 1.2em;
  color: #555;
}